import React, { PropsWithChildren, useState } from 'react';
import ReactDOM from 'react-dom';
import { useID } from '../../hooks/use-id';
import { useIsomorphicLayoutEffect } from '../../hooks/use-isomorphic-layout-effect';
import { IntrinsicElementProps } from '../../types/utility';

interface EphermeralPortalProps extends PropsWithChildren {
  className?: IntrinsicElementProps<'div'>['className'];
  style?: IntrinsicElementProps<'div'>['style'];
  root?: Element;
}

export const EphemeralPortal: React.FC<EphermeralPortalProps> = (props) => {
  const { children, className = '', style = {}, root } = props;

  const portalID = useID();
  const [portalElement, setPortalElement] = useState<HTMLDivElement | null>(null);

  // Setup/teardown the tooltip context
  useIsomorphicLayoutEffect(() => {
    const container = document.createElement('div');

    container.dataset.mgEphemeralPortal = portalID;
    container.className = className;
    for (const cssProperty of Object.keys(style)) {
      // @ts-ignore
      container.style[property] = style[property];
    }

    (root || document.body).appendChild(container);
    setPortalElement(container);

    return () => container.remove();
  }, [root, className, JSON.stringify(style)]);

  return portalElement ? <>{ReactDOM.createPortal(children, portalElement)}</> : null;
};
